@import url(https://fonts.googleapis.com/css2?family=Anaheim&family=Comfortaa&family=Jura&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.divider {
  border-top: 2px dashed black;
}

* {
  margin: 0;
  padding: 0;
}

h1.content-title {
  font-family: "Roboto", sans-serif;
}

p.content {
  font-family: "Anaheim", sans-serif;
}

.others {
  font-family: "Comfortaa", cursive;
  font-family: "Jura", sans-serif;
}

.App {
  display: flex;
  width: 750px;
  margin: 0 auto;
  justify-content: center;
}

.LeftPanel {
  border-left: 20px solid rgb(64, 58, 72);
  padding: 0px 20px 20px 20px;
  background-color: rgb(244, 242, 245);
}

.RightPanel {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0px 20px 20px 20px;
  background-color: rgb(229, 227, 232);
}

ul {
  padding-left: 20px;
}

.Portfolio {
  height: 200px;
}

div.title-underline {
  width: 40px;
  border-top: 4px solid black;
}

.title-container {
  padding-top: 20px;
  padding-bottom: 20px;
}



